.mymodal {
  position: fixed;
  top: 25%;
  left: 25%;
  transform: translate(-50%, -50%);

  border: 1px solid #ccc;
  background: rgb(104, 98, 98);
  overflow: auto;
  border-radius: 20px;
  outline: none;
  padding: 20px;
}

.myoverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
}
